body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box
}

body {
  font-family: 'Lato', sans-serif;
  background: url(../../static/media/background.f1890f7c.jpg) center/cover;
  overflow-x: hidden;
}

#element::-webkit-scrollbar {
  display: none;
}

.app-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  min-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}
.ranking-list {
	display: -webkit-flex;
	display: flex;
	width: 70%;
	-webkit-flex-direction: column;
	        flex-direction: column;
	list-style: none;
	border: 1px solid rgb(179, 179, 179);
	box-shadow: 2px 6px 10px rgb(177, 177, 177);
}

.ranking-element {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	padding: 10px;
	margin: 1px 0;
	cursor: pointer;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	border: 1px solid rgb(190, 190, 190);
	transition: 0.5s;
}


.element-city {
	width: 20%;
	font-size: 1.2rem
}

.element-parameters {
	display: block;
	width: 20%;
	font-size: 1rem;
	font-weight: 800;
}

.ranking-element:hover {
	-webkit-transform: scale(1.03);
	        transform: scale(1.03)
}

.city-description {
	display: none;
	text-align: center;
	-webkit-flex: 1 1;
	        flex: 1 1;
	margin: 5px 0;
}

.info-icon {
	width: 30px;
	height: 30px;
}

.description-bubble {
	position: relative;
}

.bubble {
	transition: visibility 1s;
	width: 300px;
	font-size: 0.7rem;
	min-height: 150px;
	background: #00A0B2;
	padding: 20px;
	color: #fff;
	position: absolute;
	top: -180px;
	right: -265px;
}

.bubble:before {
	content: "";
	width: 0px;
	height: 0px;
	position: absolute;
	border-left: 10px solid #00bfb6;
	border-right: 10px solid transparent;
	border-top: 10px solid #00bfb6;
	border-bottom: 10px solid transparent;
	left: 19px;
	bottom: -19px;
}



@media screen and (max-width: 992px) {
	.ranking-list {
		margin: 30px 0;
	}

	.ranking-element {
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-align-items: center;
		        align-items: center;
		padding: 10px;
	}

	.element-city {
		width: 100%;
		padding: 10px 0;
		font-size: 1.5rem;
		text-align: center;
	}

	.city-description {
		display: block;
	}


	.element-parameters {
		display: block;
		text-align: center;
		width: 100%;
		font-weight: 800;
	}

	.ranking-list {
		width: 90%;
	}

	.ranking-element:hover {
		-webkit-transform: none;
		        transform: none;
	}

	.description-bubble {
		display: none;
	}
}
.smoke-wrapper {
	z-index: -1;
	width: 100vw;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
}

.smoke {
	width: 150%;
	height: 120%;
	-webkit-animation: CloudMove 200s linear infinite;
	        animation: CloudMove 200s linear infinite;
	opacity: 0.8;
}


@-webkit-keyframes CloudMove {
	0% {
		-webkit-transform: translateX(-130vw);
		        transform: translateX(-130vw)
	}

	100% {
		-webkit-transform: translateX(100vw);
		        transform: translateX(100vw)
	}
}


@keyframes CloudMove {
	0% {
		-webkit-transform: translateX(-130vw);
		        transform: translateX(-130vw)
	}

	100% {
		-webkit-transform: translateX(100vw);
		        transform: translateX(100vw)
	}
}


@media screen and (max-width: 1024px) {
	.smoke {
		display: none;
	}
}
.input {
	transition: all .5s;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: center;
	        align-items: center;
	margin: 15px 0;
}

.country-input {
	background-color: rgb(31, 30, 30);
	color: white;
	padding: 20px 10px;
	display: block;
	text-align: center;
	width: 35vw;
	text-align: left;
	padding-left: 20px;
	border: 0;
	font-size: 1rem;
	border-radius: 3%;
	margin-right: 10px;
}

.button {
	cursor: pointer;
	background: #ECECEC;
	border-radius: 15px;
	padding: 20px 20px;
	display: block;
	font-weight: 800;
	color: #7f7f7f;
	font-size: 1rem;
	text-shadow: 0px 1px 0px #fff;
	border: 1px solid #a7a7a7;
	margin: 0 5px;
	width: 145px;
	box-shadow: 0px 2px 1px white inset, 0px -2px 8px white, 0px 2px 5px rgba(0, 0, 0, 0.1), 0px 8px 10px rgba(0, 0, 0, 0.1);
}

.submit {
	background-color: rgb(54, 172, 0);
}

.clear {
	background-color: rgb(173, 196, 44);

}

@media screen and (max-width: 992px) {
	.input {
		-webkit-flex-direction: column;
		        flex-direction: column;
	}

	.button {
		margin: 10px 0;
	}

	.country-input {
		width: 90vw;
	}
}
.header {
	height: 10vh;
	-webkit-flex-direction: row-reverse;
	        flex-direction: row-reverse;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.title {
	-webkit-flex: 1 1;
	        flex: 1 1;
	margin-top: 0 auto;
	font-size: 4.5rem;
	margin: 15px 15px;
	color: rgb(51, 255, 0);
	font-family: 'Jacques Francois Shadow', cursive, sans-serif;
}

.icon {
	width: 60px;
	height: 60px;
}



@media screen and (max-width: 992px) {
	.header {
		margin: 20px 0;
	}

	.title {
		font-size: 1.3rem;
	}
}
